.m-style {
  position: relative;
  text-align: center;
  zoom: 1;
  display: inline-block; }

.m-style:before,
.m-style:after {
  content: "";
  display: table; }

.m-style:after {
  clear: both;
  overflow: hidden; }

.m-style span {
  float: left;
  margin: 0 5px;
  width: 38px;
  height: 38px;
  line-height: 38px;
  color: #bdbdbd;
  font-size: 14px; }

.m-style .active {
  float: left;
  margin: 0 5px;
  width: 38px;
  height: 38px;
  line-height: 38px;
  background: #e91e63;
  color: #fff;
  font-size: 14px;
  border: 1px solid #e91e63;
  border-radius: 3px; }

.m-style a {
  float: left;
  margin: 0 5px;
  width: 38px;
  height: 38px;
  line-height: 38px;
  background: #fff;
  border: 1px solid #ebebeb;
  color: #bdbdbd;
  font-size: 14px;
  border-radius: 3px; }

.m-style a:hover {
  color: #f10180;
  background: #fcf5f7; }

.m-style .next,
.m-style .prev {
  font-family: "Simsun";
  font-size: 16px;
  font-weight: bold; }

.now,
.count {
  padding: 0 5px;
  color: #f00; }

.eg img {
  max-width: 800px;
  min-height: 500px; }

